/* 사진 및 글자의 전체 설정 값 지정  */
.list {
    /* display: flex; */
    float: left;
    /* flex-direction: column; */
    gap: 1rem;
    align-items: center;
    text-align: center;
    /* height: 350px; */
    width: 100%;
}
.main_produce .list img{
    width: 100%;  
    height: 300px;
    /* height: auto; */
    object-fit: cover;
}
