/* 텍스트 꾸미기 */
.main_produce .list .listtext {
    margin: 0 auto;
    margin-top: 1px;
    padding: 0.5px;
    line-height: 10px;
    font-size: 0.8rem;
    font-weight: bold;
    /* text-align: left; */
    color: black;
    width: 75%;
    height: 10%;
    /* text-align: center;  */
}

.main_produce:after {
    content: "";
    display: block;
    clear: both;
}
.main_produce{
    width: 35%;
    margin: 1.5%;
    text-align: center; 
    border: 1px solid #F1F6F9;
    border-radius: 5px;
    height: 10%;
    background-color: #fffbfb;
    float: left;

}